@charset "utf-8";

/* CSS Document */

/*----*****---- << General >> ----*****----*/

body { background:#f2e8c3 url(../img/top-bg.jpg) repeat; color:#4e4635; }

/*----*****---- << Headings >> ----*****----*/

h1,h2,h3,h4,h5,h6{ color:#322f20; }

h1.title, h2.title, h3.title, h4.title, h5.title, h6.title { background:url(../img/title-shadow.png) bottom no-repeat; padding-bottom:18px; color:#fffae4; text-shadow:-1px -1px 0px #634e37; margin-bottom:20px; }

.one-fourth h1.title, .one-fourth h2.title, .one-fourth h3.title, .one-fourth h4.title, .one-fourth h5.title, .one-fourth h6.title { background:url(../img/title-shadow-one-fourth.png) bottom no-repeat; }
.one-half h1.title, .one-half h2.title, .one-half h3.title, .one-half h4.title, .one-half h5.title, .one-half h6.title { background:url(../img/title-shadow-one-half.png) bottom no-repeat; }
.one-third h1.title, .one-third h2.title, .one-third h3.title, .one-third h4.title, .one-third h5.title, .one-third h6.title { background:url(../img/title-shadow-one-third.png) bottom no-repeat; }
.three-fourth h1.title, .three-fourth h2.title, .three-fourth h3.title, .three-fourth h4.title, .three-fourth h5.title, .three-fourth h6.title { background:url(../img/title-shadow-three-fourth.png) bottom no-repeat; }
.two-third h1.title, .two-third h2.title, .two-third h3.title, .two-third h4.title, .two-third h5.title, .two-third h6.title { background:url(../img/title-shadow-two-third.png) bottom no-repeat; }

h1.title span, h2.title span, h3.title span, h4.title span, h5.title span, h6.title span { background:url(../img/title-bg.jpg) repeat; }

/*----*****---- << Text Elements >> ----*****----*/

a{ color:#687719; }

blockquote{ color:#302715; border:1px solid #8f6e31; background:#b59b6d url(../img/quote.png) 10px 15px no-repeat; }
blockquote span{ color:#302715; }

blockquote.type2 { background-image:url(../img/quote2.png); }
blockquote.type3 { background-image:url(../img/quote3.png); }
blockquote.type4 { background-image:url(../img/quote4.png); }

pre, code{ border-left:5px solid #ab936a; background-color:#c8b28c; }

hr{ background-color:#160902; color:#efefef; }

/* due to IE <HR> margin bugs I had to made classed and apply them to div to achieve the effect of a <HR> */
.hr{ background:#160902; }
.hr.thin{ background:#160902; }

/*----*****---- << Tables >> ----*****----*/

th{ background:url(../img/title-bg.jpg) repeat; color:#fffae4; text-shadow:-1px -1px 0px #634e37; border-bottom:1px solid #fffae4; }
td{ background:#b39d7e; color:#302715; border-bottom:none; }
tr.even td{ background:#c1ab8b; }
tfoot td { background:#a68d69; border-bottom:4px solid #64533b; }

/*----*****---- << Forms >> ----*****----*/

label{ color:#4e4635; }

fieldset{ border:1px solid #322f20; }

input[type=text], input[type=password], input.text, textarea{ background-color:#fbf2ce; border:1px solid #bbaf9b; color:#4e4635; }
select{ background-color:#fbf2ce; border:1px solid #bbaf9b; }

input[type=submit]:hover { background:url(../img/btn-bg-hover.jpg) repeat; }

/*----*****---- << Images >> ----*****----*/

.post-content img { border:2px solid #617b00; }

.wp-caption { background:#ECE8D5; }
.wp-caption .wp-caption-text { color:#4E4635; }
.wp-caption:hover { background:#819a16; }
.wp-caption:hover .wp-caption-text { color:#fffae4; }

.rounded-img { background:#bc9b5e; }
/* .rounded-img:hover { background:#312925; } */

.green-border { border:5px solid #617b00; }
.pink-border { border:5px solid #a06c5d; }
