.error404 .content .widget ul.contact-details p a:hover, .booknow-page-sidebar .widget ul.contact-details p a:hover { color:#687719; }
.error404 .content .widget ul.contact-details span.address, .booknow-page-sidebar .widget ul.contact-details span.address { background:url(../img/addr-ico-big.png) center no-repeat; }
.error404 .content .widget ul.contact-details span.mail, .booknow-page-sidebar .widget ul.contact-details span.mail { background:url(../img/mail-ico-big.png) center no-repeat; }
.error404 .content .widget ul.contact-details span.phone, .booknow-page-sidebar .widget ul.contact-details span.phone { background:url(../img/phone-ico-big.png) center no-repeat; }

.widget.widget_calendar caption { color:#fffae4;}
.widget.widget_calendar th{ background:url(../img/title-bg.jpg) repeat; color:#fffae4; text-shadow:-1px -1px 0px #634e37; border-bottom:1px solid #b39d7e; }
.widget.widget_calendar td{ background:#a38b69; color:#4e4635; }
.widget.widget_calendar tr.even td{ background:#b39d7e; }
.widget.widget_calendar tfoot td { background:#826c53; border-bottom:4px solid #443826; }
.widget.widget_calendar a { color:#fffae4; }
.widget.widget_calendar a:hover { color:#221d1b; }

